            <p className="text-gray-500">ูู ูุชููู ูู ุงูุนุซูุฑ ุนูู ููุงูุงุช ุชุทุงุจู ูุฐุง ุงูุชุตููุฉ. ุฌุฑุจ ุชุตููุฉ ุฃุฎุฑู.</p>
